{
right:0;
} */

.close-btn{
    position:absolute;
    top:1.5rem;
    right:2.5rem;
    cursor: pointer;

}
.login form{
    background: #fff;
    border: 1rem solid rgba(0,0,0,.1);
    border: var(--border);
    width: 40rem;
    padding: 2rem;
    box-shadow: 0 .5 1rem rgba(0,0,0,.1);
    box-shadow: var(--box-shadow);
    border-radius: .5rem;
    margin: 4rem ;
    margin-top:4rem;
}

.login form h3{
    font-size: 2.5rem;
    text-transform: uppercase;
    text-align: center;
}
.login form span{
    display: block;
    font-size: 1.5rem;
    padding-top: 1.5rem;
    text-transform: uppercase;
}
.login  .box{
    margin: .7rem 0;
    width: 100%;
    font-size: 1.6rem;
    border: 1rem solid rgba(0,0,0,.1);
    border: var(--border);

}

.login .checkbox{
 
    display: flex;
    align-items: center;
    grid-gap: .5rem;
    gap: .5rem;
    padding: 1rem 0;
}

.login .checkbox label
{
    font-size: 1.5rem;
    color: #666;
    color: var(--light-color);
    cursor: pointer;
}

.login form p {
    padding-top:.8rem;
    color: #666;
    color: var(--light-color);
    font-size: 1.5rem;
}

.login form p a{
    color: #1D267D;
    color: var(--green);
    text-decoration: underline;
}


.submit-btn {
    margin-top: 1rem;
    width: 100%;
    display: inline-block;
    padding: .9rem 3rem;
    border-radius: .5rem;
    color: #fff;
    background: #1D267D;
    background: var(--green);
    font-size: 1.7rem;
    cursor: pointer;
    font-weight: 500;
    text-align: center;
    
}

